<template>
  <div id="app">
    
    <div class="image">
      <img class="loading" v-if="$store.state.loading" src="./assets/loader1.gif"/>
      <img crossorigin="anonymous" class="profile_pic" :src="linkUrl" />
    </div>
    <div class="info">
      <p><strong>{{$store.state.info.Followers}}</strong> Followers</p>
      <p><strong>{{$store.state.info.Following}}</strong> Following</p>
    </div>
    <div class="feed" v-if="!loadingFeed">
      <transition v-for="(item, i) in feed" :key="i">
        <div :class="`${item.filename.split('.jpg')[0]}`" @click="copy(item, i)" id="post" :style="`background-image: url(${item.url})`">
        </div>
      </transition>
      <div v-if="linkUrl && !feed">
        No posts / User is private.
      </div>
    </div>
    <div v-else>
      Loading Feed...
    </div>
    <form @submit="search">

    <!-- <label for="user"> Enter your Instagram handle. </label> -->
      <div class="input_wrapper">
        <p class="user_label"> @ </p>
        <input type="text" class="user" id="user" ref="text" @input="handleChange"/>
      </div>
      <br/>
      <input type="submit" :class="`submit ${inactive ? `inactive` : ``}`" v-if="!$store.state.loading"/>
      <div v-else>Loading..</div>
    </form>
    <VueRecaptcha
      :sitekey="siteKey"
      @verify="handleVerify"
      @expired="captchaVerified = false"
      @error="captchaVerified = false">
      
    </VueRecaptcha>
  </div>
 
</template>

<script>
import { VueRecaptcha } from 'vue-recaptcha';
import {mapState} from 'vuex';
export default {
  name: 'App',
  components: {
    VueRecaptcha
  },
  watch: {
    linkUrl() {
      this.captchaVerified = false;
    }
  },
  computed: {
    ...mapState(['linkUrl', 'feed'])
  },
  data() {
    return {
      loading: false,
      siteKey: "6LeJ4GshAAAAAO15LRUPXSNyPi7pRdGdWbrbbCHt",
      inactive: true,
      captchaVerified: true
    }
  },
  methods: {
    handleVerify() {
      this.captchaVerified = true;
      this.inactive = false;
    },
    handleChange() {

      // If field has text and captcha is verified  
      if (this.$refs.text.value.length > 0 && this.captchaVerified) {
        this.inactive = false;
        return;
      }
      this.inactive = true;
    },
    copy(item, i) {
      // navigator.clipboard.writeText(`https://www.instagram.com/p/${item.filename.split(".jpg")[0]}`);
      // console.log(item, document.getElementById(`post_${i}`));
      // if (document.getElementById(`post_${i}`).className.includes("active")) {
      //   document.getElementById(`post_${i}`).className = document.getElementById(`post_${i}`).className.replace(' active', '');
      //   return;
      // }
      // document.getElementById(`post_${i}`).className += ' active';
      var URLfield = [...document.getElementsByClassName("wcpa_form_item wcpa_type_text")].filter((e => e.innerText.includes("URL")))[0].getElementsByTagName("input")[0];
      URLfield.value = `https://www.instagram.com/p/${item.filename.split(".jpg")[0]}`;
      console.log(URLfield, i);
    },
    search(e) {
      e.preventDefault();
      // if (this.loading || this.$refs.text.value.length === 0 || !this.captchaVerified) return; 
      this.$store.commit("updateLoading", true);
      this.$api.profile_pic(this.$refs.text.value);
    }
  },
  mounted() {
  // // console.log(this.$store.state.linkUrl);
  // document.addEventListener("click", () => {
  //   console.log(URLfield);
  // })
  }
}
</script>

