/* eslint-disable no-unused-vars */

import { openStdin } from 'process';
import { store } from './store';
const api = "keyaJEowLnEfKAamU";
var tables = "tblSUNsZFXUQ8DS75";
const { default: axios } = require( 'axios' );
const baseId = "appYUpR8L67hyxGt6";
const url = `https://api.airtable.com/v0/${baseId}/${tables}?api_key=${api}`;

var Airtable = require('airtable');
var base = new Airtable({apiKey: api}).base(baseId);



export default new class API {
    constructor() {
        this.records;
        this.apiCounter = 0;
        this.postApiCounter = 0;
        this.options = [];
        this.idOptions = [];    
        this.init();
        // this.uploadFeed("44408919398", "reckPhobVWdvjPyIJ");
     }

    init() {
        axios.get(url).then((res) => {
            this.records = res.data.records;
        })        
    }

    async id(username) {
        return new Promise((ID) => {
            const axios = require("axios");

            const options = {
                method: 'GET',
                url: `https://instagram174.p.rapidapi.com/api/v1/user/${username}/id`,
                headers: {
                    'X-RapidAPI-Key': '6d50373928msh8ea7650abbc2053p1c1465jsn8d11a1823388',
                    'X-RapidAPI-Host': 'instagram174.p.rapidapi.com'
                }
            };
            
            axios.request(options).then(function (response) {
                ID(response.data.result);
            }).catch(function (error) {
                ID('');
            });
        })
    }

    processIdUrl(id, res) {
        return new Promise((ID) => {
            switch(id) {
                case "instagram174": {
                    ID({url: res.data.data.user.profile_pic_url, id: res.data.fbid});
                    break;
                }
            }
        })
    }

    uploadFeed(igID, AirtableID) {
        var opts = [
            {
                method: 'GET',
                url: `https://instagram203.p.rapidapi.com/userpost/${igID}/50/%7Bend_cursor%7D`,
                headers: {
                  'X-RapidAPI-Key': '6d50373928msh8ea7650abbc2053p1c1465jsn8d11a1823388',
                  'X-RapidAPI-Host': 'instagram203.p.rapidapi.com'
                },
                id: "instagram203"
            },
            {
                method: 'GET',
                url: 'https://instagram-best-experience.p.rapidapi.com/feeds',
                params: {user_id: igID, next_max_id: '100'},
                headers: {
                    'X-RapidAPI-Key': '6d50373928msh8ea7650abbc2053p1c1465jsn8d11a1823388',
                    'X-RapidAPI-Host': 'instagram-best-experience.p.rapidapi.com'
                },
                id: "instagram-best-experience"
            },
            {
                method: 'GET',
                url: 'https://instagram28.p.rapidapi.com/medias',
                params: {user_id: igID, batch_size: '50'},
                headers: {
                  'X-RapidAPI-Key': '6d50373928msh8ea7650abbc2053p1c1465jsn8d11a1823388',
                  'X-RapidAPI-Host': 'instagram28.p.rapidapi.com'
                },
                id: "instagram28"
              }
        ]
        var optsID = opts[this.postApiCounter].id;
          
          axios.request(opts[this.postApiCounter]).then(function (response) {
            var feed;
            switch(optsID) {
                case "instagram-best-experience": {
                    feed = response.data.items.map((post) => {
                        return {
                            filename: `${post.code}.jpg`, // used inthe url after https://www.instagram.com/p/_____
                            url: post.image_versions2.candidates ? post.image_versions2.candidates[0].url : ''
                        }
                    });
                    break;
                }
                case "instagram203": {
                    feed = response.data.data.edges.map((post) => {
                        return {
                            filename: `${post.node.shortcode}.jpg`,
                            url: post.node.display_url
                        }
                    })
                    break;
                }
                case "instagram28": {
                    feed = response.data.data.user.edge_owner_to_timeline_media.edges.map((post) => {
                        return {
                            filename: `${post.node.shortcode}.jpg`,
                            url: post.node.display_url
                        }
                    })
                    break;
                }
                default: {
                    feed ='default';
                    break;
                }
            }

            // Uplaod images to the feed!
            base('Table 1').update([
                {
                "id": AirtableID,
                "fields": {
                    "Feed_Images": feed
                }
            }]);

            // Output to the feed in 5 seconds after it's been uploaded
            setTimeout(() => {
                base('Table 1').find(AirtableID).then((res) => {
                    store.commit("updateFeed", res.fields.Feed_Images);
                });
                store.commit("updateLoading", false);
                store.commit("setLoadingFeed", false);
            }, 5000);



          }, (err) => {
            this.postApiCounter++;
            if (this.postApiCounter < opts.length) {
              this.uploadFeed(igID, AirtableID);
            }           
            store.commit("isPrivateProfile", true);
          });
        }


    async profile_pic(username) {
        return new Promise(() => {
            this.init();
            store.commit("setLoadingFeed", true);
            var res = this.records.filter(i => i.fields.Username === username);
            if (res.length > 0) {
                
                // If IG ID Already exists, ignore. If not, use API to get the IG ID from the username
                if (res[0].fields.ID) {
                    store.commit("updateIgID", res[0].fields.ID);
                } else {
                    this.id(username).then((resID) => {
                       this.uploadIgID(res[0].id, resID)
                    });
                }


                // If following and followers fields in Airtable exist..., and if not
                if (res[0].fields.Following && res[0].fields.Followers) {
                    store.commit("updateInfo", {Following: res[0].fields.Following, Followers: res[0].fields.Followers});
                } else {
                    this.uploadFollowInfo(res[0].id, res[0].fields);
                }

                this.getFeed(res[0]);
               

                // If profile already exists in Airtable –––––––––––––––––––––––––––––––
                if (res[0].fields.Profile_Picture) {
                    store.commit("updateLinkUrl", res[0].fields.Profile_Picture[0].url);
                }

                // If profile doesn't exist in Airtable, call API to fetch profile pic and upload into Airtable
                } else {

                    
                    this.options = [
                        {
                            // 10 / day https://rapidapi.com/v.sobolev/api/Instagram/
                            method: 'GET',
                            url: `https://instagramdimashirokovv1.p.rapidapi.com/user/${username}`,
                            headers: {
                              'X-RapidAPI-Key': '6d50373928msh8ea7650abbc2053p1c1465jsn8d11a1823388',
                              'X-RapidAPI-Host': 'InstagramdimashirokovV1.p.rapidapi.com'
                            },
                            id: "v.sobolev"
                        },
                        {
                            // 30 / day
                            method: 'GET',
                            url: `"https://instagram-fast.p.rapidapi.com/profile/"${username}`,
                            headers: {
                              'X-RapidAPI-Key': '6d50373928msh8ea7650abbc2053p1c1465jsn8d11a1823388',
                              'X-RapidAPI-Host': 'instagram-fast.p.rapidapi.com'
                            },
                            id: "instagram-fast"
                        },
                        {
                            // 20 / day https://rapidapi.com/JoTucker/api/instagram-scraper2/
                            method: 'GET',
                            url: 'https://instagram-scraper2.p.rapidapi.com/user_info' ,
                            params: {user_name: username},
                            headers: {
                              'X-RapidAPI-Key': '6d50373928msh8ea7650abbc2053p1c1465jsn8d11a1823388',
                              'X-RapidAPI-Host': 'instagram-scraper2.p.rapidapi.com'
                            },
                            id: "instagram-scraper2"
                        },   
                        {
                            // 50/month https://rapidapi.com/fariswdcash-434L2ELS8qx/api/instagram-profile1/
                            method: 'GET',
                            url: `https://instagram-profile1.p.rapidapi.com/getprofile/${username}`,
                            headers: {
                                'X-RapidAPI-Key': '6d50373928msh8ea7650abbc2053p1c1465jsn8d11a1823388',
                                'X-RapidAPI-Host': 'instagram-profile1.p.rapidapi.com'
                            },
                            id: "instagram-profile1"
                        },
                        {
                            // 200 / month https://rapidapi.com/iq.faceok/api/instagram-looter2/
                            method: 'GET',
                            url: 'https://instagram-looter2.p.rapidapi.com/profile',
                            params: {username: username},
                            headers: {
                              'X-RapidAPI-Key': '6d50373928msh8ea7650abbc2053p1c1465jsn8d11a1823388',
                              'X-RapidAPI-Host': 'instagram-looter2.p.rapidapi.com'
                            },
                            id: "instagram-looter2"
                        },

                        {
                            // 100 / month, https://rapidapi.com/yagamicell/api/instagram-data10/
                            method: 'GET',
                            url: `https://instagram-data10.p.rapidapi.com/api/rapid_api/instagram_profile/${username}`,
                            headers: {
                                id: username,
                                'X-RapidAPI-Key': '6d50373928msh8ea7650abbc2053p1c1465jsn8d11a1823388' ,
                                'X-RapidAPI-Host': 'instagram-data10.p.rapidapi.com'
                            },
                            id: "instagram-data10"
                        },
                        {
                            // 100 / month, https://rapidapi.com/omarcosramiro/api/instagram-scrapper-bulk/
                            method: 'GET',
                            url: `https://instagram-scrapper-bulk.p.rapidapi.com/profile/${username}`,
                            headers: {
                              'X-RapidAPI-Key': '6d50373928msh8ea7650abbc2053p1c1465jsn8d11a1823388' ,
                              'X-RapidAPI-Host': 'instagram-scrapper-bulk.p.rapidapi.com'
                            },
                            id: "instagram-scrapper-bulk"
                        }                     

                    ];
                      
                      
                    axios.request(this.options[this.apiCounter]).then((res) => {

                        this.processUrl(this.options[this.apiCounter].id, username, res)
                        .then((profilepicurl) => this.uploadToAirtable(username, profilepicurl)
                        .then((records) => this.findInAirtable(records)
                        .then((found) => {
                            store.commit("updateLinkUrl",   found.fields.Profile_Picture[0].url);
                            store.commit("updateInfo", {Following: found.fields.Following, Followers: found.fields.Followers});
                            this.getFeed(found);
                        })));

                    }, () => {
                        this.apiCounter += 1;
                        if (this.apiCounter <= this.options.length - 1) this.profile_pic(username);
                        // else return;

                    })
                }
            })
        
    }

    getFeed(fields) {
        // if Feed already exists, leave be –––––––––––––––––––––––––––––––
        if (fields.fields.Feed_Images) {
            store.commit("updateFeed", fields.fields.Feed_Images);
            store.commit("updateLoading", false);
            store.commit("setLoadingFeed", false);
        }
        // if no feed, [do the actions]
        else {
            // if there is an ID
            if (fields.fields.ID) {
                this.uploadFeed(fields.fields.ID, fields.id);
            }
        }
    }

 
    processUrl(id, username, res) {
        return new Promise((profilepicurl) => {
            switch(id) {
                case "instagram-fast": {
                    profilepicurl({url: res.data.data.user.profile_pic_url, id: res.data.id, Followers: res.data.edge_followed_by.count, Following: res.data.edge_follow.count});
                    break;
                }
                case "instagram-scraper2": {
                    profilepicurl({url:res.data.data.user.profile_pic_url, id: res.data.data.user.id, Followers: res.data.data.user.edge_followed_by.count, Following: res.data.data.user.edge_follow.count});
                    break;
                }
                case "instagram-profile1": {
                    profilepicurl({url:res.data.profile_pic_url, id:res.data.id, Followers: res.data.edge_followed_by.count, Following: res.data.edge_follow.count});
                    break;
                }
                case "instagram-data10": {
                    profilepicurl({url:res.data.data.profile_picture, id:res.data.data.id, Followers: res.data.data.edge_followed_by.count, Following: res.data.data.edge_follow.count});
                    break;
                }
                case "instagram-scrapper-bulk": {
                    profilepicurl({url:res.data.data.user.profile_pic_url, id:res.data.data.user.id, Followers: res.data.data.edge_followed_by.count, Following: res.data.data.edge_follow.count});
                    break;
                }
                case "v.sobolev": {
                    profilepicurl({url:res.data.profile_pic_url, id:res.data.id, Followers: res.data.edge_followed_by.count, Following: res.data.edge_follow.count});
                    break
                }
               case "instagram-scraper-2022": {
                    this.id(username).then((resID) => {
                        profilepicurl({url:res.data.user.profile_pic_url, id:resID, Followers: res.data.user.edge_followed_by.count, Following: res.data.user.edge_follow.count});
                        return;
                    });
                    break;
                }
                case "instagram-looter2": {
                    profilepicurl({url:res.data.profile_pic_url, id:res.data.id, Followers: res.data.edge_followed_by.count, Following: res.data.edge_follow.count});
                    break
                }
            }
        })
        
    }

    uploadToAirtable(username, data) {
        return new Promise((resolve) => [
            base('Table 1').create([
                {
                "fields": {
                    "Username": username,
                    "Profile_Picture": [{"url": data.url}],
                    "ID": parseInt(data.id),
                    "Following": parseInt(data.Following),
                    "Followers": parseInt(data.Followers)
                }
            }
        ],(err, records) => {
                setTimeout(() => {
                    resolve(records); // or res, gotta see which one
                }, 10000);
            })
        ]);
    }

    findInAirtable(id) {
        return new Promise((foundBase, error) => {
            base('Table 1').find(id[0].id).then((res) => {
                foundBase(res);
            }, (err) => {
                error(err);
            })
        })
        
    }

    async uploadIgID(id, IGID) {
        return new Promise((resolve) => {
            base('Table 1').update([
                {
                "id": id,
                "fields": {
                    "ID": parseInt(IGID)
                }
            }]);
            resolve();
        })
    }

    async uploadFollowInfo(id, info) {
        return new Promise((resolve) => {
            base('Table 1').update([
                {
                "id": id,
                "fields": {
                    "Following": parseInt(info.Following),
                    "Followers": parseInt(info.Followers)
                }
            }]);
            resolve();
        })
    }

    async uploadProfilePic(id, picUrl) {
        return new Promise((resolve) => {
            base('Table 1').update([
                {
                "id": id,
                "fields": {
                    "Profile_Picture": [{url: picUrl}],
                    "Airtable_ID": id
                }
            }]);
            resolve(picUrl);
        })
    }
}
