import { createStore } from 'vuex';

export const store = createStore({
    state () {
      return {
        linkUrl: '',
        feed: [],
        loading: false,
        igID: '',
        info: {
          Following: 0,
          Followers: 0
        },
        loadingFeed: false
      }
    },
    mutations: {
        updateLinkUrl (state, url) {
          state.linkUrl = url;
        },
        updateLoading (state, loading) {
            state.loading = loading;
        },
        updateFeed (state, feed) {
          state.feed = feed;
        },
        updateIgID (state, igID) {
          state.igID = igID;
        },
        isPrivateProfile(state, isPrivate) {
          state.isPrivate = isPrivate;
        },
        updateInfo(state, info) {
          state.info = info;
        },
        setLoadingFeed(state, loadingFeed) {
          state.loadingFeed = loadingFeed;
        }
      }
  })